.ayah {
	position: relative;
	display: inline-block;
	transform: translateY(12px);
	margin: 0 10px;
	user-select: none;
	width: 5vw;
	max-width: max-content;
	min-width: 30px;
}

.ayah span {
	position: absolute;
	width: 100%;
	z-index: 1;
	text-align: center;
	line-height: 1.82rem;
}

